/* ContactForm.css */

.contact-form-container {
   max-width: 400px;
   margin: 0 auto;
   padding: 20px;
   border: 1px solid #ccc;
   border-radius: 5px;
   background-color: #f9f9f9;
   box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
 }
 
 .contact-form-container h2 {
   margin-bottom: 20px;
   text-align: center;
 }
 
 .form-group {
   margin-bottom: 15px;
 }
 
 label {
   display: block;
   margin-bottom: 5px;
   margin-left: 8px;
   text-align: left;
 }
 
 input[type="text"],
 input[type="email"],
 textarea {
   width: 100%;
   padding: 10px;
   border: 1px solid #ccc;
   border-radius: 16px;
   box-sizing: border-box;
 }
 
 textarea {
   height: 100px;
 }

 .form-label{
   font-size: 16px;
   font-style: bold;
   font-weight: 800;
 }
 
 .action-button {
   display: block;
   width: 100%;
   padding: 10px;
   border: none;
   border-radius: 16px;
   background-color: #007bff;
   color: #fff;
   font-size: 16px;
   cursor: pointer;
 }
 
 .action-button:hover {
   background-color: #0056b3;
 }
 
 /* Custom Toastify styles */
 .toastify__toast-container {
   z-index: 9999;
 }
 
 .toastify__toast--success {
   background-color: #4caf50;
   color: #fff;
 }
 
 .toastify__toast--error {
   background-color: #f44336;
   color: #fff;
 }
 
 .toastify__toast-body {
   font-size: 14px;
 }
 
 .toastify__toast--elegant {
   border-radius: 5px;
   box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
 }
 